// 교육 모듈 테마 import
@use '../../styles/theme' as *;

.classroom-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: var(--bg-secondary);

  // 디버그 정보 섹션
  .debug-info {
    padding: 8px 16px;
    background: #f5f5f5;
    border-bottom: 1px solid #ddd;
    font-family: monospace;
    font-size: 11px;
  }

  .classroom-header {
    height: 64px;
    background: var(--bg-primary);
    border-bottom: 1px solid var(--border-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    z-index: 10;

    .header-left {
      display: flex;
      align-items: center;
      gap: 16px;

      .course-info {
        .course-label {
          font-size: 12px;
          color: var(--text-secondary);
          display: block;
        }
        .course-title {
          margin: 0;
          font-size: 18px;
          font-weight: 600;
          color: var(--text-primary);
        }
      }
    }

    .header-right {
      display: flex;
      align-items: center;
      gap: 24px;

      .progress-info {
        width: 200px;
        .progress-text {
          font-size: 12px;
          color: var(--text-secondary);
          margin-bottom: 4px;
          display: block;
          text-align: right;
        }
        mat-progress-bar {
          height: 6px;
          border-radius: 3px;
        }
      }

      // 학생 정보 스타일
      .student-info {
        display: flex;
        align-items: center;
        gap: 12px;
        padding: 8px 16px;
        background: linear-gradient(135deg, var(--accent-primary)20 0%, var(--accent-secondary)10 100%);
        border: 1px solid var(--accent-primary);
        border-radius: 8px;
        min-width: 240px;

        .student-icon {
          color: var(--accent-primary);
          font-size: 32px;
          width: 32px;
          height: 32px;
        }

        .student-details {
          display: flex;
          flex-direction: column;
          justify-content: center;
          gap: 2px;

          .student-id-label {
            font-size: 11px;
            color: var(--text-secondary);
            text-transform: uppercase;
            letter-spacing: 0.5px;
            font-weight: 500;
          }

          .student-id-value {
            font-size: 16px;
            font-weight: 700;
            color: var(--accent-primary);
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-family: 'Courier New', monospace;
          }

          .student-name {
            font-size: 12px;
            color: var(--text-primary);
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }

      // 정보 없음 상태
      .student-info-empty {
        background: linear-gradient(135deg, rgba(255, 193, 7, 0.13) 0%, rgba(255, 144, 2, 0.06) 100%);
        border: 1px solid #ffb74d;

        .student-icon {
          color: #ff9800;
        }

        .student-id-value {
          color: #f57c00;
        }
      }
    }
  }

  .classroom-body {
    display: flex;
    flex: 1;
    overflow: hidden;

    .mission-sidebar {
      width: 300px;
      background: var(--bg-primary);
      border-right: 1px solid var(--border-color);
      display: flex;
      flex-direction: column;

      .sidebar-header {
        padding: 20px;
        border-bottom: 1px solid var(--border-color);
        h3 {
          margin: 0;
          font-size: 16px;
          font-weight: 600;
          color: var(--text-primary);
        }
      }

      .mission-list {
        flex: 1;
        overflow-y: auto;

        .mission-item {
          padding: 16px 20px;
          display: flex;
          align-items: flex-start;
          gap: 12px;
          cursor: pointer;
          transition: background 0.2s;
          border-bottom: 1px solid var(--border-color);

          &:hover {
            background: var(--bg-tertiary);
          }

          &.active {
            background: var(--bg-tertiary);
            border-left: 4px solid var(--accent-primary);
          }

          &.introduction {
            background: rgba(76, 175, 80, 0.05);
            
            &.active {
              background: rgba(76, 175, 80, 0.1);
              border-left: 4px solid #4caf50;
            }
          }

          &.locked {
            opacity: 0.6;
            background: rgba(0, 0, 0, 0.05);
            
            &:hover {
              background: rgba(0, 0, 0, 0.05);
            }

            .mission-text {
              .title {
                color: var(--text-tertiary);
              }
            }
          }

          &.completed {
            opacity: 0.7;
            background: rgba(76, 175, 80, 0.08);
            
            &:hover {
              background: rgba(76, 175, 80, 0.08);
            }

            .mission-text {
              .title {
                color: #4caf50;
                text-decoration: line-through;
              }
            }
          }

          &.submitted {
            opacity: 0.7;
            background: rgba(33, 150, 243, 0.08);
            
            &:hover {
              background: rgba(33, 150, 243, 0.08);
            }

            .mission-text {
              .title {
                color: #2196f3;
              }
            }
          }

          .mission-status {
            margin-top: 2px;
            .done { color: var(--success-color); font-size: 20px; width: 20px; height: 20px; }
            .pending { color: var(--text-tertiary); font-size: 20px; width: 20px; height: 20px; }
            .intro-icon { color: #4caf50; font-size: 20px; width: 20px; height: 20px; }
            .locked-icon { color: #ff9800; font-size: 20px; width: 20px; height: 20px; }
            .submitted-icon { color: #2196f3; font-size: 20px; width: 20px; height: 20px; }
          }

          .mission-text {
            display: flex;
            flex-direction: column;
            .week {
              font-size: 11px;
              color: var(--accent-primary);
              font-weight: 600;
              text-transform: uppercase;
            }
            .intro-label {
              font-size: 11px;
              color: #4caf50;
              font-weight: 600;
              text-transform: uppercase;
            }
            .title {
              font-size: 14px;
              color: var(--text-primary);
              margin-top: 2px;
            }
            .locked-badge {
              font-size: 10px;
              color: #ff9800;
              font-weight: 600;
              margin-top: 4px;
              padding: 2px 6px;
              background: rgba(255, 152, 0, 0.15);
              border-radius: 4px;
              display: inline-block;
              width: fit-content;
            }
            .completed-badge {
              font-size: 10px;
              color: #4caf50;
              font-weight: 600;
              margin-top: 4px;
              padding: 2px 6px;
              background: rgba(76, 175, 80, 0.15);
              border-radius: 4px;
              display: inline-block;
              width: fit-content;
            }
            .submitted-badge {
              font-size: 10px;
              color: #2196f3;
              font-weight: 600;
              margin-top: 4px;
              padding: 2px 6px;
              background: rgba(33, 150, 243, 0.15);
              border-radius: 4px;
              display: inline-block;
              width: fit-content;
            }
          }
        }
      }
    }

    .mission-content {
      flex: 1;
      overflow-y: auto;
      padding: 40px;
      display: flex;
      justify-content: center;

      .loading-state {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: var(--text-secondary);
        gap: 16px;
      }

      // 미션 선택 안 된 초기 상태
      .empty-mission-state {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        min-height: 500px;

        .empty-state-container {
          text-align: center;
          color: var(--text-secondary);

          .empty-icon {
            font-size: 80px;
            width: 80px;
            height: 80px;
            color: var(--text-tertiary);
            margin-bottom: 24px;
            opacity: 0.5;
          }

          h2 {
            margin: 0 0 12px 0;
            font-size: 24px;
            font-weight: 600;
            color: var(--text-primary);
          }

          p {
            margin: 0;
            font-size: 14px;
            color: var(--text-secondary);
            line-height: 1.6;
          }
        }
      }

      .mission-detail {
        width: 100%;
        max-width: 800px;

        .detail-header {
          margin-bottom: 32px;
          padding-bottom: 24px;
          border-bottom: 2px solid var(--border-color);

          &.introduction-header {
            background: linear-gradient(135deg, rgba(76, 175, 80, 0.08), rgba(76, 175, 80, 0.04));
            border: 2px solid rgba(76, 175, 80, 0.3);
            border-radius: 12px;
            padding: 24px;
            border-bottom: 2px solid rgba(76, 175, 80, 0.3);
          }

          .title-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            h2 {
              margin: 0;
              font-size: 28px;
              font-weight: 700;
              color: var(--text-primary);
            }
            .points-badge {
              background: var(--warning-color);
              color: #fff;
              padding: 4px 12px;
              border-radius: 16px;
              font-size: 13px;
              font-weight: 600;
              border: none;
            }
            .intro-badge {
              background: linear-gradient(135deg, #4caf50, #66bb6a);
              color: #fff;
              padding: 6px 14px;
              border-radius: 20px;
              font-size: 12px;
              font-weight: 600;
              border: none;
              display: flex;
              align-items: center;
              gap: 6px;
            }
          }
          .description {
            font-size: 16px;
            color: var(--text-primary);
            line-height: 1.6;
            margin: 0;
          }
        }

        .mission-activity {
          margin-top: 32px;
          background: var(--bg-primary);
          border-radius: 12px;
          box-shadow: 0 2px 12px var(--shadow);
          padding: 32px;
          border: 1px solid var(--border-color);

          &.empty {
            text-align: center;
            padding: 60px;
            color: var(--text-secondary);
            mat-icon {
              font-size: 48px;
              width: 48px;
              height: 48px;
              margin-bottom: 16px;
            }
          }

          .activity-header {
            display: flex;
            align-items: center;
            gap: 12px;
            margin-bottom: 24px;
            color: var(--accent-primary);
            mat-icon { font-size: 24px; }
            h3 { margin: 0; font-size: 18px; font-weight: 600; color: var(--text-primary); }

            &.intro-activity {
              color: #4caf50;
            }
          }

          .survey-container {
            .survey-info {
              margin-bottom: 32px;
              h4 { margin: 0 0 8px 0; font-size: 20px; color: var(--text-primary); }
              p { margin: 0; color: var(--text-secondary); }
            }

            .survey-form {
              .question-item {
                margin-bottom: 32px;
                
                .question-label {
                  display: block;
                  font-size: 16px;
                  font-weight: 500;
                  margin-bottom: 12px;
                  color: var(--text-primary);
                  .required { color: var(--error-color); margin-left: 4px; }
                }

                .full-width { width: 100%; }

                .radio-group {
                  display: flex;
                  flex-direction: column;
                  gap: 12px;
                }

                .rating-group {
                  padding: 0 10px;
                  mat-slider { width: 100%; }
                  .rating-labels {
                    display: flex;
                    justify-content: space-between;
                    font-size: 12px;
                    color: #999;
                    margin-top: -8px;
                  }
                }
              }

              .form-actions {
                margin-top: 40px;
                display: flex;
                justify-content: flex-end;
                
                button {
                  padding: 0 32px;
                  height: 48px;
                  font-size: 16px;
                  border-radius: 24px;
                  display: flex;
                  align-items: center;
                  gap: 8px;
                }
              }
            }
          }
        }
      }

      // 제출 결과 페이지
      .result-page {
        width: 100%;
        max-width: 800px;
        padding: 40px 0;

        .result-container {
          background: linear-gradient(135deg, rgba(76, 175, 80, 0.05), rgba(76, 175, 80, 0.02));
          border: 2px solid rgba(76, 175, 80, 0.3);
          border-radius: 16px;
          padding: 40px;
          text-align: center;

          .result-header {
            margin-bottom: 40px;

            .success-icon {
              font-size: 64px;
              width: 64px;
              height: 64px;
              color: #4caf50;
              margin-bottom: 16px;
            }

            h2 {
              margin: 0 0 8px 0;
              font-size: 28px;
              font-weight: 700;
              color: var(--text-primary);
            }

            .result-message {
              margin: 0;
              font-size: 16px;
              color: var(--text-secondary);
            }
          }

          .result-info {
            background: var(--bg-primary);
            border-radius: 12px;
            padding: 20px;
            margin-bottom: 32px;
            border-left: 4px solid #4caf50;
            text-align: left;

            h3 {
              margin: 0 0 12px 0;
              font-size: 18px;
              font-weight: 600;
              color: var(--text-primary);
            }

            .submit-time {
              margin: 0;
              font-size: 14px;
              color: var(--text-secondary);
            }
          }

          .result-answers {
            text-align: left;
            margin-bottom: 32px;

            h4 {
              font-size: 16px;
              font-weight: 600;
              color: var(--text-primary);
              margin: 0 0 16px 0;
            }

            .answer-list {
              .answer-item {
                background: var(--bg-primary);
                border-radius: 8px;
                padding: 16px;
                margin-bottom: 12px;
                border-left: 4px solid var(--accent-primary);

                .answer-question {
                  display: flex;
                  gap: 8px;
                  margin-bottom: 12px;

                  .question-num {
                    font-weight: 600;
                    color: var(--accent-primary);
                    min-width: 40px;
                  }

                  .question-text {
                    font-size: 14px;
                    color: var(--text-primary);
                    flex: 1;
                  }
                }

                .answer-value {
                  padding-left: 40px;
                  font-size: 14px;
                  color: var(--text-primary);
                  background: var(--bg-secondary);
                  padding: 12px;
                  border-radius: 6px;
                  word-break: break-word;

                  .answer-text {
                    display: block;
                    line-height: 1.6;
                  }

                  .answer-choice {
                    display: block;
                    color: #4caf50;
                    font-weight: 500;
                  }

                  .answer-rating {
                    display: block;
                    color: #ff9800;
                    font-weight: 500;
                  }
                }
              }
            }
          }

          .result-actions {
            display: flex;
            justify-content: center;
            gap: 12px;

            button {
              padding: 0 32px;
              height: 48px;
              font-size: 16px;
              border-radius: 24px;
              display: flex;
              align-items: center;
              gap: 8px;
            }
          }
        }
      }
    }
  }

  // ========== 제출 목록 페이지 스타일 ==========
  .submission-list-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow-y: auto;
    padding: 32px;
    background: var(--bg-secondary);

    .submissions-container {
      max-width: 1000px;
      margin: 0 auto;
      width: 100%;

      .submissions-header {
        text-align: center;
        margin-bottom: 32px;

        .success-icon {
          font-size: 48px;
          width: 48px;
          height: 48px;
          color: #4caf50;
          margin-bottom: 16px;
        }

        h2 {
          margin: 0 0 8px 0;
          font-size: 28px;
          font-weight: 600;
          color: var(--text-primary);
        }

        .submissions-subtitle {
          margin: 0;
          font-size: 14px;
          color: var(--text-secondary);
        }
      }

      .submissions-stats {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        gap: 16px;
        margin-bottom: 32px;

        .stat-item {
          background: var(--bg-primary);
          padding: 16px;
          border-radius: 8px;
          border: 1px solid var(--border-color);
          display: flex;
          flex-direction: column;
          gap: 8px;

          .stat-label {
            font-size: 12px;
            color: var(--text-secondary);
            font-weight: 500;
          }

          .stat-value {
            font-size: 28px;
            font-weight: 600;
            color: var(--text-primary);
          }
        }
      }

      .submissions-table {
        background: var(--bg-primary);
        border: 1px solid var(--border-color);
        border-radius: 8px;
        overflow: hidden;
        margin-bottom: 24px;

        .empty-state {
          padding: 48px 24px;
          text-align: center;
          color: var(--text-secondary);

          mat-icon {
            font-size: 48px;
            width: 48px;
            height: 48px;
            margin-bottom: 16px;
            opacity: 0.5;
          }

          p {
            margin: 0;
            font-size: 14px;
          }
        }

        .table-wrapper {
          overflow-x: auto;

          table {
            width: 100%;
            border-collapse: collapse;
            font-size: 13px;

            thead {
              background-color: var(--bg-secondary);
              border-bottom: 2px solid var(--border-color);

              th {
                padding: 12px 16px;
                text-align: left;
                font-weight: 600;
                color: var(--text-primary);
                white-space: nowrap;
              }
            }

            tbody {
              tr {
                border-bottom: 1px solid var(--border-color);
                transition: background-color 0.2s;

                &:hover {
                  background-color: rgba(0, 0, 0, 0.02);
                }

                &.late {
                  background-color: rgba(255, 152, 0, 0.05);
                }

                td {
                  padding: 12px 16px;
                  color: var(--text-primary);

                  &.index {
                    color: var(--text-secondary);
                    font-weight: 500;
                    width: 40px;
                  }

                  &.assignment-name {
                    font-weight: 500;
                    max-width: 200px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                  }

                  &.status {
                    .badge-DRAFT {
                      background-color: #e0e0e0;
                      color: #424242;
                    }

                    .badge-SUBMITTED {
                      background-color: #e3f2fd;
                      color: #1976d2;
                    }

                    .badge-GRADING {
                      background-color: #fff3e0;
                      color: #f57c00;
                    }

                    .badge-GRADED {
                      background-color: #e8f5e9;
                      color: #388e3c;
                    }

                    .badge-RETURNED {
                      background-color: #ffebee;
                      color: #d32f2f;
                    }

                    .badge-RESUBMITTED {
                      background-color: #f3e5f5;
                      color: #7b1fa2;
                    }

                    span {
                      display: inline-block;
                      padding: 4px 12px;
                      border-radius: 16px;
                      font-size: 12px;
                      font-weight: 500;
                    }
                  }

                  &.submit-time {
                    color: var(--text-secondary);
                    font-size: 12px;
                  }

                  &.attempt {
                    text-align: center;
                    font-weight: 500;
                  }

                  &.late-badge {
                    .badge-late {
                      color: #f57c00;
                      font-weight: 600;
                    }

                    .badge-ontime {
                      color: #4caf50;
                      font-weight: 600;
                    }
                  }

                  &.score {
                    font-weight: 600;

                    .score-value {
                      color: #4caf50;
                      font-size: 14px;
                    }

                    .score-pending {
                      color: var(--text-secondary);
                      font-size: 12px;
                      font-weight: 500;
                    }
                  }

                  &.actions {
                    text-align: center;

                    button {
                      mat-icon {
                        color: var(--text-secondary);
                      }

                      &:hover mat-icon {
                        color: var(--text-primary);
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      .submissions-actions {
        display: flex;
        justify-content: center;
        gap: 12px;

        button {
          min-width: 200px;
          height: 40px;
        }
      }
    }
  }
}